import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue'
import uniSegmentedControl from "@/components/uni-segmented-control/uni-segmented-control.vue";
export default {
	components: {
		uniSegmentedControl,uniLoadMore
	},
	data() {
		return {
			items: ['未使用','已使用','已失效'],
			current: 0,
			page: 1,
			limit: 10,
			listData: [],
			loadStatus: 'more'
		}
	},
	onLoad() {
		this.getData();
	},
	onReachBottom () {
		if (this.loadStatus === 'more') {
			this.getData();
		}
	},
	methods: {
		// tab点击切换
		onClickItem(index) {
			if (this.current !== index) {
				this.current = index;
				this.page = 1;
				this.listData = [];
				this.getData();
			}
		},
		//获取优惠券列表
		getData() {
			this.loadStatus = 'loading'
			let data = {
				page: this.page,
				limit: this.limit
			}
			if (this.current == 0) {
				data['display'] = 'no_used';
			}
			if (this.current == 1) {
				data['display'] = 'yes_used';
			}
			if (this.current == 2) {
				data['display'] = 'invalid';
			}
			this.$api.userCoupon(data, res => {
				if (res.status) {
					let now_type = 'no_used';
					if (this.current == 1) {
						now_type = 'yes_used';
					}
					if (this.current == 2) {
						now_type = 'invalid';
					}
					if (now_type == res.data.q_type) {
						if (res.data.page >= this.page) {
							let newList = this.listData.concat(res.data.list);
							this.listData = newList;
							if (res.data.count > this.listData.length) {
								this.page ++
								this.loadStatus = 'more'
							} else {
								this.loadStatus = 'noMore'
							}
						}
					}
				}else{
					this.$common.errorToShow(res.msg);
				}
			});
		},
		//跳转首页
		goIndex() {
			uni.switchTab({
				url: '/pages/index/index'
			});
		}
	}
}
